import { Link } from 'react-router-dom';
export const AuthInscription = () => {
    return <form>
        <section className="formSegment">
            <label>Nom</label>
            <input type="text" placeholder="AaZz..." />
        </section>
        <section className="formSegment">
            <label>Prenom</label>
            <input type="text" placeholder="AaZz..." />
        </section>
        <section className="formSegment">
            <label>Mail</label>
            <input type="text" placeholder="mail@gmail.com" />
        </section>
        <section className="formSegment">
            <label>Sexe</label>
            <select>
                <option>Homme</option>
                <option>Femme</option>
            </select>
        </section>
        <div className="formBtn">
            <button className='semiRounded'>Connexion</button>
        </div>
        <div className='formOtherAuth'>
            <p>Vous avez deja un compte ? <Link to='/Authentification/Connexion'>Connexion</Link> </p>
        </div>
    </form>
}